/* #### Main Landing Section ################################################################################################## */





#header{
    width: 100%;
    height: 80vh;
    margin-top: 70px;
}

#header h1{
    margin:0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    color: black
}

#header .brand-name{
    color: #3498db;
}

#header h2{
    color: #484848;
    font-size: 24px;
}

#header .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
}

#header .btn-get-started:hover{
    background: #3498db;
    color: white;
}

.header-img{
    text-align: right;
}

#header .animated{
    /* animation: name duration timing-function delay iteration-count direction fill-mode; */
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}

@keyframes up-down {
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}

/* ### contact css ### */
.contact_div{
    width:100%;
    height: 75vh;
}

/* ### Footer css ### */
.footer{
    margin: 0;
    padding:0;
}

/* end */

/* Responsive Code */

@media (max-width:991px){
    #header{
        height:80vh;
        text-align: center;
    }

    #header .header-img{
        text-align: center;
    }

    #header .header-img img{
        width: 60%;
    }
}

@media (max-width:768px){
    #header{
        margin-top: 20px;
    }
    #header h1{
        font-size: 28px;
    }
    #header h2{
        font-size: 18px;
        margin-bottom: 30px;
    }
    #header .header-img img{
        width:70%;
    }
}
/* end */


















